.home-page-container {
  width: 100%;
}

.overview {
  width: 100%;
  height: 40em;
  background: url("../../assets/simon-abrams-k_T9Zj3SE8k-unsplash.jpg")
    no-repeat center;
  background-size: cover;
  display: flex;
  align-items: flex-end;
}
.profile-wrap {
  width: 80%;
  text-align: center;
  font-size: 2rem;
  margin: 1em auto;
  color: azure;
  font-family: "Caladea";
}
.experience {
  background: url(/static/media/safar-safarov-MSN8TFhJ0is-unsplash.683488d1.jpg)
    no-repeat;
  background-size: cover;
  height: 42em;
}
.projects {
  background: url("../../assets/alexandru-acea-GhwCef9VRr4-unsplash.jpg")
    no-repeat;
  background-size: cover;
  height: 80em;
}
.expericne-wrap {
  width: 80%;
  font-size: 1rem;
  margin: 1em auto;
  background: #ddd;
  padding: 1em;
  border-radius: 1em;
  box-shadow: 0 10px 10px 0 #e2e2e2;
}
.skills-wrap {
  width: 80%;
  font-size: 1rem;
  margin: 1em auto;
  height: 23em;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}
.skills-wrap ul {
  margin: 0;
}
.skill-set-item {
  background: #f2eedd;
  padding: 1em;
  border-radius: 1em;
}
.projects-wrap {
  width: 80%;
  font-size: 1rem;
  margin: 1em auto;
}
.projects-wrap ul{
  list-style: none;
  padding-left: 0;
}
.projects-wrap-card {
  background: #fffdfd;
  padding: 1em;
  box-shadow: 0 10px 10px 0 #e2e2e2;
  border-radius: 1em;
  margin: 2em 0;
}
.section-header {
  width: 80%;
  margin: 1em auto;
  color: #6fbaf1;
  text-decoration: underline;
  text-transform: uppercase;
  padding-top: 1em;
}
.education {
  /* margin-top: 11em; */
}
.interest-hobbies-list {
  width: 76%;
  margin: 1em auto;
  padding: 1em;
  border-radius: 1em;
  box-shadow: 5px 5px 10px 5px #e2e2e2;
}
.footer {
  color: #fff;
  margin: 2em auto;
  background: #272626;
  font-family: "Libre Franklin";
}
.footer h1 {
  text-align: center;
  margin: 0;
  padding-top: 2em;
  font-size: 2em;
}
.scl-links-wrap img {
  width: 2em;
  margin: 0.75em;
}
.scl-links-wrap p {
  text-align: center;
  text-transform: capitalize;
}
.about-info-wrap {
  display: flex;
  justify-content: space-between;
  width: 87%;
  margin: 0 auto;
  padding: 3em;
}
.scl-links-wrap {
  display: flex;
  justify-content: space-between;
}
